.button{
    margin: 11px 0 25px 75px  ;
    width: 100px;
    border-radius: 10px;

}


.ldsFacebook {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
}
.ldsFacebook div {
    display: inline-block;
    position: absolute;
    left: 8px;
    width: 16px;
    background-color: rgba(18,47,170,0.6);
    animation: ldsFacebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
}
.ldsFacebook div:nth-child(1) {
    left: 8px;
    animation-delay: -0.24s;
}
.ldsFacebook div:nth-child(2) {
    left: 32px;
    animation-delay: -0.12s;
}
.ldsFacebook div:nth-child(3) {
    left: 56px;
    animation-delay: 0ms;
}
@keyframes ldsFacebook {
    0% {
        top: 8px;
        height: 64px;
    }
    50%, 100% {
        top: 24px;
        height: 32px;
    }
}
